.pb-dashboard-form {
    padding-top: 10px;
    position: relative;
}

/** Tabs **/
.pb-db-tabs ul {
    width: 100%;
    height: auto;
    list-style: none;
    margin: 0px;
    padding: 0px;
    li {
        display: inline-block;
        margin-bottom: 0px;
        a {
            background: #f8fafb;
            color: #252525;
            padding: 19px 22px;
            font-size: 15px;
            font-weight: 600;
            display: block;
            border-radius: 5px 5px 0 0;
            text-decoration: none;
            margin-right: 7px;
        }
    }
}

.tabs__is-active a {
    background: #fff !important;
}

.pb-tabs__content {
    background: #fff;
    padding: 15px 25px;
}

/** blocks **/
.pb-blocks-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-flow: row wrap;
    flex-flow: row wrap;
    overflow: hidden;
    margin-left: -10px;
    margin-right: -40px;
    .pb-w__item {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        width: calc(25% - 57px);
        background: #f8fafb;
        border: 1px solid #e8e8e8;
        margin-bottom: 20px;
        margin-right: 15px;
        border-radius: 5px;
        padding: 20px;
    }
    .pb-w-i__icon {
        display: inline-block;
        width: 25px;
        svg {
            height: 20px;
            width: 20px;
            vertical-align: middle;
        }
    }
    .pb-w-i__name {
        display: inline-block;
        font-size: 15px;
        color: #313131;
        font-weight: 500;
        a i {
            font-size: 14px;
            vertical-align: middle;
            margin-left: 5px;
        }
    }

}


@media screen and (max-width: 782px) {
    .pb-blocks-list .pb-w__item {
        width: 100%;
    }
}


.pb-blocks-list .pb-w-i__toggle {
    position: absolute;
    right: 5px;
    top: 35px;
}

.pb-w-s__btn {
    position: absolute;
    right: 10px;
    top: 10px;
    input[type='submit'] {
        padding: 10px;
        background: linear-gradient(to right, #f30d55 0%, #5820e5 100%);
        color: #fff;
        font-size: 14px;
        width: 150px;
        border-radius: 5px;
        border: none;
        font-weight: 500;
        &:hover {
            background: linear-gradient(to right, #f30d55 0%, #5820e5 100%);
            opacity: 0.8;
            color: #fff;
        }
    }
}

@media screen and (max-width: 782px) {
    .pb-w-s__btn input[type='submit'] {
        width: 120px;
    }
}

.pb-w-i__toggle input[type="checkbox"].pb-toggle {
    font-size: 16px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 45px;
    height: 24px;
    background: #ddd;
    border-radius: 3em;
    position: relative;
    cursor: pointer;
    outline: none;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    border: none;
}

.pb-w-i__toggle input[type="checkbox"].pb-toggle:checked {
    background: -webkit-gradient(linear, left top, right top, from(#f30d55), to(#5820e5));
    background: linear-gradient(to right, #f30d55 0%, #5820e5 100%);
}

.pb-w-i__toggle input[type="checkbox"].pb-toggle:checked::before {
    content: '' !important;
}

.pb-w-i__toggle input[type="checkbox"].pb-toggle:after {
    position: absolute;
    content: "";
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    background: #fff;
    -webkit-box-shadow: 0 0 0.25em rgba(0, 0, 0, 0.3);
    box-shadow: 0 0 0.25em rgba(0, 0, 0, 0.3);
    -webkit-transform: scale(0.7);
    transform: scale(0.7);
    left: 0;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.pb-w-i__toggle input[type="checkbox"].pb-toggle:checked:after {
    left: calc(100% - 1.5em);
}

